#portfolio .tiles {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-areas: "tile1 tile1 tile2" "tile3 tile4 tile4" "tile5 tile5 tile5";
  grid-template-rows: calc(1.5 * 280px) 280px calc(1.2 * 280px);
  grid-gap: 20px; }
  #portfolio .tiles .tile {
    height: 280px;
    grid-column: span 1;
    grid-row: span 2;
    height: 100%;
    width: 100%;
    text-align: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    box-shadow: 0px 0px 35px 0px rgba(0, 0, 0, 0.18);
    cursor: pointer; }
    #portfolio .tiles .tile .wrapper {
      width: 100%;
      height: 100%;
